.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.Icon-container-check {
    position: absolute;
    left: 12px;
}

.Icon-container-check--active {
    color: #4caf50;
}

.Icon-container-delete {
    position: absolute;
    top: -24px;
    right: 0;
}

.Icon-container-delete:hover {
    color: red;
}

.Icon-svg {
    width: 24px;
    height: 24px;
}

.Icon-container-check:hover {
    color: green;
    transition: color 0.3s ease, background-color 0.3s ease; /* Transición para ambos */
}

.Icon-container-delete:hover {
    color: red;
    transition: color 0.3s ease, background-color 0.3s ease; /* Transición para ambos */
}

  .Icon-container-check:hover .Icon-svg path { /* Selecciona la ruta del SVG */
    fill: white !important; /* Fuerza el color de relleno a blanco */
}

  .Icon-container-delete:hover .Icon-svg path { /* Selecciona la ruta del SVG */
    fill: white !important; /* Fuerza el color de relleno a blanco */
}